import {Badge, Button, FormControl, FormGroup, FormLabel} from "react-bootstrap";
import {IFileInput} from "./IFileInput";
import {useFormContext} from "react-hook-form";
import {Paperclip} from "react-bootstrap-icons";
import React, {MutableRefObject, useRef} from "react";

export const FileInput = (props: IFileInput) => {
    const {register, formState: {errors}} = useFormContext();
    const fileInputRef = useRef<HTMLInputElement | null>(null)

    const isRequired = props.isRequired || false;
    const isMultiple = props.isMultiple || false;

    const { ref, ...rest } = register(props.name, {
        required: {value: isRequired, message: "Выберите файл для загрузки."}
    });

    return <FormGroup {...props.attributes}>
        {props.label &&
            <FormLabel>
                {props.label}
            </FormLabel>
        }
        <FormControl type="file" {...rest} ref={(event: any) => {
            ref(event)
            fileInputRef!.current = event
        }} multiple={isMultiple} className="d-none"/>
        <Button variant="dark"
                className="me-1"
                onClick={() => fileInputRef.current?.click()}>
            <Paperclip/>
        </Button>
        {errors[props.name]?.message &&
            <Badge bg="warning">{errors[props.name]?.message?.toString()}</Badge>
        }
    </FormGroup>
}
