import {action, observable} from "mobx";
import {getAsync} from "../../../../common/fetch/getAsync";
import {postAsync} from "../../../../common/fetch/postAsync";

const ticketViewState = observable({
    ticket: null as unknown as ITicketView
});

const getTicketView = action(async (id: string) => {
    return getAsync(`/Ticket/GetTicket?ticketId=${id}`).then(response => {
        ticketViewState.ticket = response.data;
    })
})

const sendMessageAsync = async (id: string, form: any) => {

    const formData = new FormData()
    formData.append("ticketId", id)
    formData.append("message", form.message)

    for(let i = 0; i < form.files.length; i++) {
        formData.append(`files[${i}]`, form.files.item(i)!)
    }

    if(form.message === "")
        return

    await postAsync("/Ticket/AddMessage", formData, () => {
        getTicketView(id)
    })
}

export { ticketViewState, getTicketView, sendMessageAsync }

interface ITicketView{
    id:number,
    title: string,
    ticketMessages: IMessage[]
}

interface IMessage{
    id: number,
    userId: number,
    message: string,
    date: string,
    files: string[]
}
