import {useFormContext} from "react-hook-form";
import {FormCheck, FormGroup} from "react-bootstrap";
import {ICheckboxInput} from "./ICheckboxInput";
import {useEffect} from "react";

export const CheckboxInput = (props: ICheckboxInput) => {
    const {register, setValue} = useFormContext()

    useEffect(() => {
        setValue(props.name, props.checked)
    }, [props.checked]);

    return <FormGroup {...props.attributes}>
        <FormCheck {...register(props.name, {
        })} label={props.label} defaultChecked={props.checked}/>
    </FormGroup>
}