import {sendMessageAsync, ticketViewState} from "./store";
import {MessageList} from "./component/MessageList";
import {observer} from "mobx-react-lite";
import {Button, Form, FormGroup} from "react-bootstrap";
import {useParams} from "react-router-dom";
import React from "react";
import {getTranslate} from "../../../../common/utils/localization";
import {FormProvider, useForm} from "react-hook-form";
import {TextAreaInput} from "../../../../common/component/input/text-area/TextAreaInput";
import {FileInput} from "../../../../common/component/input/file/FileInput";
import {authState} from "../../../../common/store/auth";
import {userRole} from "../../../../common/store/model/IUser";

export const Index = observer(() => {
    const {ticketId} = useParams();
    const hookForm = useForm();

    return <div>
        <div>
            <h4>#{ticketViewState.ticket?.id} - {ticketViewState.ticket?.title}</h4>
        </div>
        <hr className="my-0"/>
        <MessageList/>
        <div className="d-flex w-100">
            <FormProvider {...hookForm}>
                <Form onSubmit={hookForm.handleSubmit((form) => sendMessageAsync(ticketId!, form))}
                      className="d-flex w-100 align-items-end">
                    <TextAreaInput rows={2}
                                   name="message"
                                   placeholder={getTranslate("Введите сообщение")}
                                   attributes={{className: "me-1 w-100"}}/>
                    {authState.user.role == userRole.admin &&
                        <FileInput name="files"
                                   isMultiple={true}/>
                    }
                    <FormGroup>
                        <Button variant="dark"
                                type="submit">
                            {getTranslate("Отправить")}
                        </Button>
                    </FormGroup>
                </Form>
            </FormProvider>
        </div>
    </div>
})
