import {action, observable} from "mobx";
import {getAsync} from "../../../common/fetch/getAsync";
import {closeLastModal} from "../../../common/component/modal/modalStore";
import {successToast} from "../../../common/toast/success";
import {postAsync} from "../../../common/fetch/postAsync";

const settingsState = observable({settingsState: null as unknown as IUserSettings})

const getSettingsAsync = action(async () => {
    return await getAsync("/User/GetSettings").then(response => {
        settingsState.settingsState = response.data
    })
})

const addChangePasswordRequestAsync = action(async (form: any) => {
    const md5 = require('md5');

    const afterPost = () => {
        closeLastModal()
        successToast("Запрос на смену пароля успешно создан. Проверьте электронную почту, на нее отправлено подтверждение.");
    }

    await postAsync<Object>("User/AddChangePasswordRequest", {
        password: md5(form.password)
    }, afterPost)
})

const addChangeEmailRequestAsync = action(async (form: any) => {
    const afterPost = () => {
        closeLastModal()
        successToast("Запрос на смену электронной почты успешно создан. Проверьте электронную почту, на нее отправлено подтверждение.");
    }

    await postAsync<Object>("User/AddChangeEmailRequest", {
        email: form.email
    }, afterPost)
})

const updateTelegramAsync = action(async (id: number) => {
    await postAsync("/User/SaveTelegramId", {telegramId: id}, async () => {
        await getSettingsAsync()
    })
})

const saveNotificationAsync = action(async (form: any) => {
    await postAsync("/User/SaveNotificationSettings", form, () => {
        successToast("Успешно сохранено")
    })
})

export {
    settingsState,
    getSettingsAsync,
    addChangePasswordRequestAsync,
    addChangeEmailRequestAsync,
    updateTelegramAsync,
    saveNotificationAsync
}

interface IUserSettings{
    email: string,
    telegramId: number,
    useTelegramNotificationAboutNews: boolean,
    useTelegramNotificationLoginInError: boolean,
    useTelegramNotificationTicketMessage: boolean,
    useEmailNotificationAboutNews: boolean,
    useEmailNotificationLoginInError: boolean,
    useEmailNotificationTicketMessage: boolean
}