import {observer} from "mobx-react-lite";
import {getTranslate} from "../../../../../common/utils/localization";
import {Button} from "react-bootstrap";

export const Other = observer(() => {
    return <>
        <div className="mt-2">
            <h6>{getTranslate("Прочее")}</h6>
            <hr className="mt-1"/>
        </div>
        <div className="d-flex w-100">
            <div className="d-flex w-100 align-items-end">

            </div>
            <div className="d-flex w-100 align-items-end mx-2">
                <div>
                    <Button variant="danger">{getTranslate("Удалить аккаунт")}</Button>
                </div>
            </div>
        </div>
    </>
})