import {Badge, Button} from "react-bootstrap";
import {state} from "../store";
import {observer} from "mobx-react-lite";
import {openModal} from "../../../../common/component/modal/modalStore";
import {PaymentModal} from "./payment-modal/PaymentModal";
import {size} from "../../../../common/component/modal/IModal";
import {getTranslate} from "../../../../common/utils/localization";
import {ProfileLoader} from "./loaders/ProfileLoader";

export const Profile = observer(() => {

    return <div>
        {state.state &&
            <>
                <div className="d-block d-lg-flex justify-content-around">
                    <div className="flex-fill">
                        <div>
                            <span className="fw-bold-light">{getTranslate("Логин")}:</span> {state.state?.login}
                        </div>

                        <div>
                            <span
                                className="fw-bold-light">{getTranslate("Электронная почта")}: </span>{state.state?.email}
                        </div>
                        <div>
                            <span
                                className="fw-bold-light">{getTranslate("Дата регистрации")}: </span>{new Date(state.state?.registerDate).toLocaleDateString()}
                        </div>
                    </div>
                    <div className="flex-fill">
                        <div>
                            <span
                                className="fw-bold-light">{getTranslate("Реферальные бонусы")}: </span>{state.state?.balance}
                        </div>
                        <div>
                            <span className="fw-bold-light">{getTranslate("Доступ")}: </span>

                            {state.state?.isExpired &&
                                <Badge bg="secondary">{getTranslate("Бесплатный доступ")}</Badge>
                            }

                            {!state.state?.isExpired &&
                                <Badge
                                    bg="success">{getTranslate("Премиум доступ до")} {new Date(state.state?.endAccessDate).toLocaleDateString()}</Badge>
                            }

                        </div>
                    </div>
                </div>
                <div className="flex-column d-lg-flex justify-content-between mt-3">
                    <div className="d-flex justify-content-around justify-content-lg-start">
                        <Button variant="dark"
                                size="sm" onClick={() => openModal({modal: <PaymentModal/>, size: size.lg})}>
                            {state.state?.isExpired ? getTranslate("Повысить до премиума") : getTranslate("Продлить премиум")}
                        </Button>
                    </div>
                </div>
            </>
        }

        {!state.state &&
            <ProfileLoader/>
        }

        <hr/>
    </div>
})
