import {observer} from "mobx-react-lite";
import {Profile} from "./component/profile/Profile";
import {Notification} from "./component/notification/Notification";
import {getTranslate} from "../../../common/utils/localization";
import {Other} from "./component/other/Other";
import {authState} from "../../../common/store/auth";
import {userRole} from "../../../common/store/model/IUser";

export const Index = observer(() => {
    return <>
        <div>
            <h4>{getTranslate("Настройки")}</h4>
        </div>
        <hr className="mt-0"/>
        <Profile/>
        <Notification/>
        {authState.user.role == userRole.admin &&
            <Other/>
        }
    </>
})