import {observer} from "mobx-react-lite";
import {getTranslate} from "../../../../../common/utils/localization";
import {Button, Form, FormControl, FormLabel} from "react-bootstrap";
import {FormProvider, useForm} from "react-hook-form";
import {saveNotificationAsync, settingsState, updateTelegramAsync} from "../../store";
import {CheckboxInput} from "../../../../../common/component/input/checkbox/CheckboxInput";
import {SendCheckFill, SendSlashFill} from "react-bootstrap-icons";
import {useRef} from "react";

export const Notification = observer(() => {
    const hookForm = useForm()
    const telegramIdRef = useRef<HTMLInputElement>(null);

    return <>
        <FormProvider {...hookForm}>
            <Form onSubmit={hookForm.handleSubmit(saveNotificationAsync)}>
                <div className="mt-2">
                    <h6>{getTranslate("Уведомления")}</h6>
                    <hr className="mt-1"/>
                </div>
                <div className="d-flex mb-3">
                    <div className="w-100 mx-2"></div>
                    <div className="w-100 text-center mx-2">{getTranslate("Электронная почта")}</div>
                    <div className="w-100 text-center mx-2">
                        <span className="me-2">{getTranslate("Телеграм")}</span>
                        {settingsState.settingsState?.telegramId > 0 &&
                            <Button onClick={() => updateTelegramAsync(0)}
                                    title={getTranslate("Отвязать телеграм")}
                                    size="sm"
                                    variant="dark">
                                <SendSlashFill/>
                            </Button>
                        }
                    </div>
                </div>
                <div className="d-flex flex-column justify-content-center mb-1">
                    <div className="d-flex mx-2">
                        <div className="w-100">
                            <FormLabel>{getTranslate("Получать важные события, новости и обновления")}</FormLabel>
                        </div>
                        <CheckboxInput
                            name="useEmailNotificationAboutNews"
                            checked={settingsState.settingsState?.useEmailNotificationAboutNews}
                            attributes={{className: "d-flex justify-content-center w-100"}}/>

                        {settingsState.settingsState?.telegramId == 0 &&
                            <div className="w-100">
                            </div>
                        }

                        {settingsState.settingsState?.telegramId > 0 &&
                            <CheckboxInput
                                name="useTelegramNotificationAboutNews"
                                checked={settingsState.settingsState?.useTelegramNotificationAboutNews}
                                attributes={{className: "d-flex justify-content-center w-100"}}/>
                        }
                    </div>
                    <div className="d-flex mx-2">
                        <div className="w-100">
                            <FormLabel>{getTranslate("Уведомлять при неудочных попытках входа в аккаунт")}</FormLabel>
                        </div>
                        <CheckboxInput
                            name="useEmailNotificationLoginInError"
                            checked={settingsState.settingsState?.useEmailNotificationLoginInError}
                            attributes={{className: "d-flex justify-content-center w-100"}}/>

                        {settingsState.settingsState?.telegramId == 0 &&
                            <div className="w-100 text-center">
                                <div className="d-flex justify-content-center">
                                    <FormControl ref={telegramIdRef}
                                                 placeholder={getTranslate("Введите код")}
                                                 style={{maxWidth: "150px"}}
                                                 className="me-2"/>
                                    <Button variant="dark">
                                        <SendCheckFill onClick={() => updateTelegramAsync(Number(telegramIdRef.current?.value || 0))}/>
                                    </Button>
                                </div>
                            </div>
                        }

                        {settingsState.settingsState?.telegramId > 0 &&
                            <CheckboxInput
                                name="useTelegramNotificationLoginInError"
                                checked={settingsState.settingsState?.useTelegramNotificationLoginInError}
                                attributes={{className: "d-flex justify-content-center w-100"}}/>
                        }
                    </div>
                    <div className="d-flex mx-2">
                        <div className="w-100">
                            <FormLabel>{getTranslate("Уведомлять о новых сообщениях в тикете")}</FormLabel>
                        </div>
                        <CheckboxInput
                            name="useEmailNotificationTicketMessage"
                            checked={settingsState.settingsState?.useEmailNotificationTicketMessage}
                            attributes={{className: "d-flex justify-content-center w-100"}}/>

                        {settingsState.settingsState?.telegramId == 0 &&
                            <div className="w-100 text-center">
                                <Button variant="link"
                                        className="text-black"
                                        size="sm"
                                        href="https://t.me/lock_vpn_bot"
                                        target="_blank">
                                    {getTranslate("Получить код привязки")}
                                </Button>
                            </div>
                        }

                        {settingsState.settingsState?.telegramId > 0 &&
                            <CheckboxInput
                                name="useTelegramNotificationTicketMessage"
                                checked={settingsState.settingsState?.useTelegramNotificationTicketMessage}
                                attributes={{className: "d-flex justify-content-center w-100"}}/>
                        }
                    </div>
                </div>
                <div className="mt-2">
                    <Button variant="dark" className="mx-2" type="submit">
                        {getTranslate("Сохранить")}
                    </Button>
                    <Button variant="danger" className="mx-2">
                        {getTranslate("Отключить все оповещения")}
                    </Button>
                </div>
            </Form>
        </FormProvider>

    </>
})