import ContentLoader from "react-content-loader";

export const ProfileLoader = () => {
    return   <ContentLoader viewBox="0 0 380 40">
        {/* Only SVG shapes */}
        <rect x="0" y="0" rx="5" ry="5" width="300" height="10" />
        <rect x="0" y="15" rx="4" ry="4" width="300" height="10" />
        <rect x="0" y="30" rx="3" ry="3" width="300" height="10" />
    </ContentLoader>
}