import {observer} from "mobx-react-lite";
import {ticketViewState} from "../store";
import {authState} from "../../../../../common/store/auth";
import Viewer from 'react-viewer';



export const MessageList = observer(() => {
    return <div className="ticket-message-list">
        {
            ticketViewState.ticket?.ticketMessages.map(message => {
                const bg = message.userId === Number(authState.user.id) ? "bg-second-light text-white" : "bg-light";

                return <div key={message.id} className={`my-2 w-100 ${bg} p-2 border rounded-2 border-0`}>
                    {message.message}

                    {message.files &&
                        <Viewer images={message.files.map(x => {
                            return {
                                src: x,
                                alt: ""
                            }
                        })}/>
                    }
                </div>
            })
        }
    </div>
})
